.app-wrapper {
  box-sizing:border-box ;
  margin: 0 auto;
  width: 100%;
  

  display: grid;
  grid-template-areas: 
  'header header'
  'nav content';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap:10px;  */
}

.app-wrapper-content {
  
    grid-area: content;
    background-color: cornflowerblue;

}
